/* index.css */
/*
сustom properties для темы
*/
:root {
  /* Светлая тема по умолчанию */
  --color-bg: #ffffff;
  --color-text: #1a1a1a;
  
  /* Переменные для шапки (Header) */
  --color-header-bg: rgba(255, 255, 255, 0.7); /* Glassmorphism */
  --color-header-border: #e5e7eb;
  --color-nav-link: #636a75d8;
  --color-nav-link-hover: #000;
  --color-burger: #374151; /* Цвет линий бургера */
  --color-mobile-menu-bg: rgba(255, 255, 255, 0.95); /* Фон мобильного меню */

  --color-accent-bg: #374151;
  --color-accent-text: #ffffff;
}

/* Тёмная тема (Применяется, когда body имеет класс "dark-theme") */
.dark-theme {
  --color-bg: #1a1a1a; 
  --color-text: #ffffff;
  --color-header-bg: rgba(26, 26, 26, 0.7); 
  --color-header-border: #374151;
  --color-nav-link: #d1d5dbd8;
  --color-nav-link-hover: #ffffff;
  --color-burger: #ffffff;
  --color-mobile-menu-bg: rgba(26, 26, 26, 0.95);

  --color-accent-bg: #f3f4f6;
  --color-accent-text: #1a1a1a;
}



html {
  scroll-behavior: smooth;
  overflow-y: scroll; 
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-bg);
  color: var(--color-text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background-color 0.3s ease, color 0.3s ease; 
}

.section-padding {
  padding: 8rem 0;
}

.primary-button {
  background-color: var(--color-accent-bg);
  color: var(--color-accent-text);
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem; /* 6px */
  font-weight: 600;
  text-decoration: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
  border: none;
  cursor: pointer;
}

.primary-button:hover {
  opacity: 0.85;
  transform: translateY(-1px);
}

*:focus {
  outline: 2px dashed var(--color-accent-bg);
  outline-offset: 4px;
}